.user {
    color: inherit;
    text-decoration: none;
    padding:8px 12px;
    display: flex;
    justify-content: space-between;
    align-items: center;
    border-bottom: 1px solid #dbdbdb;
    gap: 24px;

    &:hover {
        background-color: #e8f0fe;
    }
}

.userName {
    flex: 1;
}